
<template>
  <div class="wrap">
    <div class="nav" >
      <div @click="back"><i class="iconfont icon-fanhui" style="font-size: 0.36rem; font-weight: bold;"></i></div>
      <div style="font-size: 0.5rem color: #000;" @click="clientShare"><i class="iconfont icon-fenxiang" style="font-size: 0.4rem;font-weight: bold;"> 分享</i></div>
    </div>
      <div class="platform">
        <img src="../assets/imgs/1.png" alt="">
      </div>
      <div class="title">
        <img src="../assets/imgs/2.png" alt="">
      </div>
      <div class="tv">
        <img src="../assets/imgs/3.png" alt="">
        <iframe src = "https://xsgbdstxinmeiti.oss-cn-beijing.aliyuncs.com/hb.mp4" allow = "autoplay"/>
        <!-- <video controls autoplay src="https://xsgbdstxinmeiti.oss-cn-beijing.aliyuncs.com/hb.mp4" ></video> -->
      </div>
      <div class="qrcode">
        <img src="../assets/imgs/4.png" alt="">
      </div>
  </div>
</template>

<script>

export default {
  data () {
    return {
      nav: true,
      title: '【视频】什么才是萧山人的“生活标配”？这份大数据告诉你！',
      img_src: 'https://xsgbdstxinmeiti.oss-cn-beijing.aliyuncs.com/list-top-mobile.jpg',
      introduce: '简介：近年来，萧山区以“绿水青山就是金山银山”理念为指引，坚定不移推进美丽萧山行动。'
    }
  },
  methods: {
    hideHeader () { //  隐藏原生导航栏
      window.SZJSBridge ? window. PalauAPI.view.hideHeader() : this.nav = false
    },
    clientShare () { // 客户端分享
      if (window.SZJSBridge) {
        window.PalauAPI.share('all', this.title, this.introduce, location.href, this.img_src)
      }
    },
    back () {
      window.PalauAPI.back()
    },
    wxShare () {
      this.$http({
          url: 'https://xsgbdstxinmeiti.oss-cn-beijing.aliyuncs.com/hbr.mp4',
          data: this.$qs.stringify({
            href: window.location.href
          }),
          method: 'post'
        }).then(res => {
            res.data.jsApiList = ['onMenuShareAppMessage', 'onMenuShareTimeline']
            wx.config(res.data)
            wx.ready(() => {
                var news_title = this.title
                var news_link = location.href
                var news_image = this.img_src
                var news_intro = this.introduce
                wx.onMenuShareAppMessage({
                  title: news_title,
                  desc: news_intro,
                  link: news_link,
                  imgUrl: news_image,
                  success () {
                    this.$message({
                      message: '分享成功',
                      type: 'success',
                      center: true
                    })
                  },
                  fail () {
                      this.$message({
                          message: '分享失败',
                          type: 'warning',
                          center: true
                      })
                  }
                })
                wx.onMenuShareTimeline({
                    title: news_title,
                    link: news_link,
                    imgUrl: news_image,
                    success () {
                    this.$message({
                        message: '分享成功',
                        type: 'success',
                        center: true
                    })
                    },
                    fail () {
                        this.$message({
                            message: '分享失败',
                            type: 'warning',
                            center: true
                        })
                    }
                })
            })  
        })
    },
    play () {

    }
  },
  mounted () {
    this.hideHeader()
    this.wxShare()
  }
}
</script>

<style scoped>
.wrap {
  width: 100%;
  height: 100vh;
  background: url('../assets/imgs/bg.png');
  background-size: 100% 100%;
}
.nav {
  font-size: 0.6rem;
  padding: 0.1rem 0.4rem ;
  height: 4vh;
  /* background: #ffffff; */
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.platform {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.platform > img {
  width: 60%;
}
.title {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 0.55rem;
}
.title >img {
  width: 84%;
}
.tv {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 0.9rem;
  position: relative;
  overflow: hidden;
  height: 5rem;
}
.tv img {
  width: 98%;
  height: 100%;
}
.tv iframe{
  position: absolute;
  width: 95%;
  height: 4.2rem;
  top: 0.1rem;
  left: 0;
  right: 0;
  margin: 0 auto;
  background: #000;
  border: none;
}
.qrcode {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 0.4rem;
  position: relative;
  overflow: hidden;
}
.qrcode img {
  width: 25%;
}
</style>



