import Vue from 'vue'
import Router from 'vue-router'

import index from '@/pages/index'

Vue.use(Router)

export default new Router({
  mode: 'history',
  baseUrl: '',
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    }
  ]
})



